.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;

  &__logo {
    display: flex;
    margin-left: 50px;
  }
}

.nav {
  margin-right: 50px;

  &__list {
    display: flex;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    margin: 0 10px;
    position: relative;
  
    &:last-child {
      margin-right: 0;
    }
  
    &:first-child {
      margin-left: 0;
    }
  }

  &__link {
    color: #060b35;
    font-size: 12px;
    text-decoration: none;
    text-transform: uppercase;
    padding: 23px 0;
    display: inline-block;

    &:hover {
      color: #00acdc;
      transition-duration: 300ms;
    }
  }

  & .is-active {
    color: #00acdc;
  }
}

.is-active::after {
  position: absolute;
  content: "";
  display: block;
  background-color: #00acdc;
  height: 4px;
  width: 100%;
  bottom: 0;
  border-radius: 8px;
}
